// API Debug Utility - Helps track API calls and errors

export const apiDebug = {
  // Log API request
  logRequest: (config) => {
    if (process.env.NODE_ENV === 'development') {
      console.group(`ðŸš€ API Request: ${config.method?.toUpperCase()} ${config.url}`);
      console.log('Config:', {
        url: config.url,
        method: config.method,
        timeout: config.timeout,
        headers: config.headers,
        data: config.data,
        timestamp: new Date().toISOString()
      });
      console.groupEnd();
    }
  },

  // Log API response
  logResponse: (response) => {
    if (process.env.NODE_ENV === 'development') {
      console.group(`âœ… API Response: ${response.config.method?.toUpperCase()} ${response.config.url}`);
      console.log('Response:', {
        status: response.status,
        statusText: response.statusText,
        data: response.data,
        headers: response.headers,
        duration: response.config?.duration || 'N/A',
        timestamp: new Date().toISOString()
      });
      console.groupEnd();
    }
  },

  // Log API error with full details
  logError: (error) => {
    console.group(`âŒ API Error: ${error.config?.method?.toUpperCase() || 'UNKNOWN'} ${error.config?.url || 'UNKNOWN'}`);
    
    const errorInfo = {
      message: error.message,
      code: error.code,
      name: error.name,
      timestamp: new Date().toISOString(),
      config: {
        url: error.config?.url,
        method: error.config?.method,
        timeout: error.config?.timeout,
        baseURL: error.config?.baseURL,
      }
    };

    if (error.response) {
      // Server responded with error
      errorInfo.response = {
        status: error.response.status,
        statusText: error.response.statusText,
        data: error.response.data,
        headers: error.response.headers,
      };
      console.error('Server Response Error:', errorInfo);
    } else if (error.request) {
      // Request made but no response
      errorInfo.request = {
        timeout: error.code === 'ECONNABORTED',
        networkError: !error.response,
      };
      console.error('Network/Timeout Error:', errorInfo);
    } else {
      // Error setting up request
      console.error('Request Setup Error:', errorInfo);
    }

    console.groupEnd();
    return errorInfo;
  },

  // Format error message for user display
  formatErrorMessage: (error) => {
    if (error.response?.data) {
      const errorData = error.response.data;
      
      // Timeout error
      if (error.code === 'ECONNABORTED' || errorData.timeout) {
        return 'Request timeout. Server is taking too long to respond. Please try again.';
      }

      // Network error
      if (errorData.networkError || !error.response) {
        return 'Network error. Please check your internet connection or server status.';
      }

      // Server error message
      if (errorData.message) {
        return errorData.message;
      }

      // Validation errors
      if (errorData.errors) {
        if (Array.isArray(errorData.errors)) {
          return errorData.errors.map(err => 
            typeof err === 'string' ? err : err.msg || err.message || JSON.stringify(err)
          ).join(', ');
        }
        if (typeof errorData.errors === 'object') {
          return Object.values(errorData.errors).join(', ');
        }
      }

      // Status code based messages
      switch (error.response.status) {
        case 400:
          return 'Invalid request. Please check your input.';
        case 401:
          return 'Unauthorized. Please login again.';
        case 403:
          return 'Access denied. You do not have permission.';
        case 404:
          return 'Resource not found.';
        case 500:
          return 'Server error. Please try again later.';
        default:
          return `Error ${error.response.status}: ${error.response.statusText || 'Unknown error'}`;
      }
    }

    // Network/timeout errors
    if (error.code === 'ECONNABORTED') {
      return 'Request timeout. Please try again.';
    }

    if (!error.response) {
      return 'Network error. Please check your connection.';
    }

    return error.message || 'An unexpected error occurred. Please try again.';
  }
};

export default apiDebug;

