import axios from 'axios';
import Cookies from 'js-cookie';
import apiDebug from './apiDebug';

// Create axios instance
const api = axios.create({
  baseURL: process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api',
  timeout: 30000, // Increased to 30 seconds for POST requests
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor to add auth token
api.interceptors.request.use(
  (config) => {
    const token = Cookies.get('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    // If FormData, remove Content-Type to let axios set it with boundary
    if (config.data instanceof FormData) {
      delete config.headers['Content-Type'];
    }
    
    // Log request in development
    apiDebug.logRequest(config);
    
    // Track request start time
    config.startTime = Date.now();
    
    return config;
  },
  (error) => {
    apiDebug.logError(error);
    return Promise.reject(error);
  }
);

// Response interceptor to handle errors
api.interceptors.response.use(
  (response) => {
    // Calculate duration
    if (response.config.startTime) {
      response.config.duration = Date.now() - response.config.startTime;
    }
    apiDebug.logResponse(response);
    return response;
  },
  (error) => {
    // Calculate duration for errors too
    if (error.config?.startTime) {
      error.config.duration = Date.now() - error.config.startTime;
    }
    
    // Enhanced error logging using debug utility
    const errorInfo = apiDebug.logError(error);

    // Handle timeout errors
    if (error.code === 'ECONNABORTED' || error.message.includes('timeout')) {
      console.error('Request timeout - Server took too long to respond');
      const timeoutError = {
        ...error,
        response: {
          ...error.response,
          data: {
            status: 'error',
            message: 'Request timeout. Server is taking too long to respond. Please try again.',
            timeout: true
          }
        }
      };
      return Promise.reject(timeoutError);
    }

    // Handle network errors
    if (!error.response) {
      console.error('Network error - No response from server');
      const networkError = {
        ...error,
        response: {
          data: {
            status: 'error',
            message: 'Network error. Please check your internet connection or server status.',
            networkError: true
          }
        }
      };
      return Promise.reject(networkError);
    }

    if (error.response?.status === 401) {
      // Token expired or invalid
      Cookies.remove('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

export default api;
